<script>

  export default {
    data: function () {
      return {
        isBlock: false
      };
    },
    methods: {
      popupBlock: function(){
        this.isBlock = !this.isBlock;
      },
      popupNone: function(){
        var userIcon = document.querySelector(".fa-user-large");
        
        userIcon.classList.remove("clickIcon");
        this.isBlock = false;
      },
      addIcon: function(thisIcon){
        var target = thisIcon.target;
        target.classList.toggle("clickIcon");
      },
      slideMenuNone: function(xmark){
        var target = xmark.target;
        var menu = target.parentElement;
        var shopping = document.querySelector(".fa-cart-shopping");
        var heart = document.querySelector(".fa-heart");

        menu.style.transform = "translateX(110%)";
        shopping.classList.remove("clickIcon");
        heart.classList.remove("clickIcon");
      },
      cartMenu: function(){
        var cartMenu = document.querySelector(".cartMenu");
        cartMenu.style.transform = "translateX(0)"
      },
      heartMenu: function(){
        var heartMenu = document.querySelector(".heartMenu");
        heartMenu.style.transform = "translateX(0)"
      }
    }
  }

</script>

